* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
  font-size: 13.5px;
  color: #14223d;
}

section {
  padding: 0 15px;
}

@media (min-width: 768px) {
  section {
    padding: 0 20px;
  }
}

@media (min-width: 1200px) {
  section {
    padding: 0 250px;
  }
}

h1.decoration {
  font-size: 26px;
  letter-spacing: -1px;
}

@media (min-width: 768px) and (max-width: 1200px) {
  h1.decoration {
    font-size: 34px;
  }
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.decoration {
  position: relative;
}

.decoration::after {
  position: absolute;
  content: "";
  border-top: 3px solid #fc3b56;
  width: 60px;
  top: 135%;
  left: 50%;
  transform: translate(-50%);
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  padding-left: 15px;
  padding-right: 5px;
}

@media (min-width: 768px) {
  .page-header {
    height: 85px;
    padding: 0 20px;
  }
}

@media (min-width: 1200px) {
  .page-header {
    height: 60px;
    padding: 0 250px;
  }
}

.page__logo {
  margin: 0;
}

.page__logo img {
  height: 40px;
}

@media (min-width: 768px) {
  .page__logo img {
    height: 50px;
  }
}

@media (min-width: 1200px) {
  .page__logo img {
    height: 35px;
  }
}

.login__button {
  background-color: #fc3b56;
  border: none;
  color: #fff;
  font-size: 15px;
  font-weight: 600;
  padding: 10px 30px;
  border-radius: 5px;
  margin-bottom: 0;
  cursor: pointer;
  transition: 0.2s 0.1s ease-in-out;
  display: none;
  font-size: 12px;
  padding: 8px 28px;
}

.login__button:hover {
  background-color: #fb092b;
}

@media (min-width: 1200px) {
  .login__button {
    display: inline;
  }
}

.login__button--mobile {
  display: inline;
}

@media (min-width: 1200px) {
  .login__button--mobile {
    display: none;
  }
}

.hamburger {
  padding: 10px;
  display: inline-block;
  cursor: pointer;
  background-color: transparent;
  border: 0;
  margin: 0;
}

@media (min-width: 1200px) {
  .hamburger {
    display: none;
  }
}

.hamburger,
.navigation {
  transition: transform 0.3s 0.1s ease-in-out;
}

.hamburger__box {
  width: 35px;
  height: 25px;
  display: inline-block;
  position: relative;
}

.hamburger__inner {
  width: 100%;
  height: 2.5px;
  border-radius: 10px;
  background-color: #152743;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: background-color 0.1s 0.2s ease-in-out;
}

.hamburger__inner::before,
.hamburger__inner::after {
  width: 100%;
  height: 2.5px;
  border-radius: 10px;
  background-color: #152743;
  position: absolute;
  content: "";
  left: 0;
  transition: transform 0.2s 0.2s ease-in-out;
}

.hamburger__inner::before {
  top: -10px;
}

.hamburger__inner::after {
  top: 10px;
}

.hamburger--active .hamburger__inner {
  background-color: transparent;
}

.hamburger--active .hamburger__inner:before {
  transform: translateY(10px) rotate(45deg);
}

.hamburger--active .hamburger__inner:after {
  transform: translateY(-10px) rotate(-45deg);
}

.navigation {
  height: 100vh;
  width: 80vw;
  background-color: #fafafa;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-80vw);
  z-index: 2;
}

@media (max-width: 1200px) {
  .navigation {
    text-align: center;
  }
}

@media (min-width: 1200px) {
  .navigation {
    position: static;
    transform: translateX(0);
    background-color: inherit;
    z-index: 0;
    height: initial;
    width: initial;
    align-self: center;
  }
}

.navigation--active {
  transform: translateX(0px);
}

.navigation__list {
  padding-top: 50px;
}

@media (max-width: 1200px) {
  .navigation__list {
    text-align: left;
  }
}

@media (min-width: 1200px) {
  .navigation__list {
    padding-top: initial;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.navigation__item {
  margin-bottom: 20px;
}

.navigation__item a {
  color: #14223d;
  font-size: 12px;
  font-weight: 600;
  margin: 0 14px;
  transition: 0.1s 0.1s ease-in-out;
  padding-bottom: 5px;
}

.navigation__item a:hover {
  border-bottom: 3px solid #6ba5c6;
}

@media (min-width: 1200px) {
  .navigation__item {
    margin-bottom: initial;
  }
}

