.passions {
  text-align: center;
  padding-top: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.passions__header {
  margin-bottom: 80px;
}

@media (min-width: 768px) and (max-width: 1200px) {
  .passions__header {
    margin-bottom: 100px;
  }
}

@media (min-width: 1200px) {
  .passions__header {
    margin-top: 25px;
    margin-bottom: 105px;
  }
}

.passions__container {
  text-align: center;
}

@media (min-width: 768px) {
  .passions__container {
    display: flex;
    justify-content: space-around;
    width: 100%;
  }
}

.passions__passion {
  padding: 80px 0 100px 0;
  position: relative;
  width: 100%;
  font-weight: 400;
}

.passions__passion::after {
  content: "";
  display: block;
  background: no-repeat url("../../images/Pattern_horizontal.png");
  background-size: 300px;
  background-position: -60px -62px;
  z-index: -1;
  position: absolute;
  width: 220px;
  height: 220px;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (min-width: 1200px) {
  .passions__passion::after {
    background-size: 230px;
    background-position: -25px -20px;
  }
}

@media (min-width: 768px) and (max-width: 1200px) {
  .passions__passion {
    padding: 100px 0 65px 0;
  }
}

@media (min-width: 1200px) {
  .passions__passion {
    padding: 60px 0 0 0;
  }
}

.passion__name {
  font-weight: 600;
  font-size: 18px;
}

@media (min-width: 768px) {
  .passion__name {
    font-size: 16px;
  }
}

@media (min-width: 1200px) {
