  }
}

@media (min-width: 1200px) {
  .passion__description {
    padding: 0 32px;
    font-size: 11px;
  }
}

.passion__development::before {
  position: absolute;
  bottom: 70%;
  left: 50%;
  transform: translate(-50%);
  content: url("../../images/icon_development.svg");
}

@media (min-width: 768px) and (max-width: 1200px) {
  .passion__development::before {
    bottom: 75%;
  }
}

@media (min-width: 1200px) {
  .passion__development::before {
    bottom: 60%;
    transform: translate(-50%) scale(0.75);
  }
}

.passion__audyt::before {
  position: absolute;
  bottom: 70%;
  left: 50%;
  transform: translate(-50%);
  content: url("../../images/icon_audyt.png");
}

@media (min-width: 768px) and (max-width: 1200px) {
  .passion__audyt::before {
    bottom: 75%;
  }
}

@media (min-width: 1200px) {
  .passion__audyt::before {
    bottom: 60%;
    transform: translate(-50%) scale(0.75);
  }
}

.passion__consulting::before {
  position: absolute;
  bottom: 70%;
  left: 50%;
  transform: translate(-50%);
  content: url("../../images/icon_consulting.png");
}

@media (min-width: 768px) and (max-width: 1200px) {
  .passion__consulting::before {
    bottom: 75%;
  }
}

@media (min-width: 1200px) {
  .passion__consulting::before {
    bottom: 60%;
    transform: translate(-50%) scale(0.75);
  }
