label.form__rodo {
  display: none;
  font-weight: 300;
  cursor: pointer;
  margin-bottom: 15px;
}

@media (min-width: 1200px) {
  label.form__rodo {
    display: unset;
  }
}

label.form__rodo input {
  height: 10px;
  width: 10px;
  position: relative;
  margin-right: 5px;
}

label.form__rodo input::before {
  position: absolute;
  content: "";
  display: block;
  width: inherit;
  height: inherit;
  top: -50%;
  left: -50%;
  transform: translate(25%, 25%);
  cursor: pointer;
  transition: all 0.4s ease;
  background-color: #fff;
  border: 2px solid #d4dbe9;
  border-radius: 3px;
}

label.form__rodo input:checked:before {
  background-color: #296ad0;
}

