  display: none;
  padding-top: 45px;
}

@media (min-width: 1200px) {
  .partners {
    display: block;
  }
}

.partners__header {
  margin-bottom: 40px;
}

.partners__container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  border: 0.5px solid #d4dbe9;
}

.partners__partner {
  display: block;
  margin: 0;
  border: 0.5px solid #d4dbe9;
  height: 125px;
  transition: 0.5s 0.1s ease-in;
}

.partners__partner:hover {
  background-color: #e5e9f2;
  z-index: 2;
  transform: scale(1.2);
}

.partner__cocacola {
  background: no-repeat url("../../images/cocacola.png");
  background-size: 70%;
