.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  color: #6b81ad;
  font-size: 12px;
  font-weight: 600;
}

@media (min-width: 768px) and (max-width: 1200px) {
  .footer {
    padding-top: 40px;
  }
}

@media (min-width: 1200px) {
  .footer {
    padding: 65px 250px 5px 250px;
    font-size: 11px;
  }
}

.footer__line {
  width: calc(100% - 30px);
  border: 0.5px solid #d4dbe9;
}

@media (min-width: 768px) and (max-width: 1200px) {
  .footer__line {
    width: calc(100% - 40px);
    margin-bottom: 24px;
  }
}

@media (min-width: 1200px) {
  .footer__line {
    width: 100%;
  }
}

.footer__links {
  width: 100%;
}

@media (min-width: 768px) {
  .footer__links {
    padding: 0 20px 10px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 1200px) {
  .footer__links {
    padding: 0 0 10px 0;
  }
}

.footer__logo {
  margin: 0;
}

@media (max-width: 768px) {
  .footer__logo {
    padding-top: 20px;
  }
}

@media (min-width: 1200px) {
  .footer__logo img {
    height: 10px;
  }
}

.footer__navigation {
  padding: 0;
}

@media (min-width: 768px) {
  .footer__navigation {
    display: flex;
    margin: 0 30px 0 0;
  }
}

.footer__navigation__item {
  margin-bottom: 25px;
}

@media (min-width: 768px) {
  .footer__navigation__item {
    margin-bottom: 0;
  }
}

.footer__navigation__item a {
  color: #6b81ad;
  margin: 0 5px;
  font-size: 12px;
  font-weight: 300;
  transition: 0.1s 0.1s ease-in-out;
}

.footer__navigation__item a:hover {
  color: #3f5073;
  border-bottom: 2px solid #3f5073;
  padding-bottom: 9px;
}

@media (min-width: 768px) {
  .footer__navigation__item a {
    margin: 0 20px;
  }
}

@media (min-width: 1200px) {
  .footer__navigation__item a {
    font-size: 11px;
  }
}

.footer__icons {
  margin-top: 12px;
}

.footer__icons img {
  margin: 0 5px;
}

@media (min-width: 1200px) {
  .footer__icons img {
    height: 16px;
    margin: 0;
    margin-left: 10px;
  }
}

@media (min-width: 768px) {
  .footer__icons {
    margin-top: 0;
  }
}

@media (min-width: 768px) {
  .footer__copyright {
    margin-bottom: 30px;
  }
}

@media (min-width: 1200px) {
  .footer__copyright {
    font-size: 9px;
    margin-bottom: 5px;
  }
}
